/*CSS DIRECTLY TO HTML COMPONENTS*/

.App {
  text-align: center;
}

.flexBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.robot {
  height: 150px;
  margin-top: 10px;
  padding-top: 0px;
