}

.headType {
  margin-top: 20px;
  margin-bottom: 20px;
}

#holeType {
  margin-top: 20px;
}

h4 {
  margin-top: 20px;
  font-size: 20px;
}

.css-qfz70r-MuiFormGroup-root {
  justify-content: center;
}

.break {
