.tableContainer {
  display: flex;
  justify-content: center;
}
span {
  font-size: 12px;
}
th {
  font-family: "robotoBold", Arial, Helvetica, sans-serif;
  font-size: 18px;
}
/*FASTENER INFO STYLING*/
.headType {
  display: flex;
  justify-content: center;
}
.headCard {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
}

/*Hide head type radio buttons*/
.headType [type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/*Style Images*/
[type="radio"] + img {
  cursor: pointer;
}

/*Style Checked Image*/
[type="radio"]:checked + img {
  outline: 2px solid black;
}

img {
  height: 140px;
  padding: 20px;
  border-radius: 5px;
}

/*FOOTER*/
.footerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3em;
}
svg {
  margin-left: 10px;
  height: 3em;
}
a {
  display: flex;
  align-items: center;
}
